<template>
  <div class="main-container">
    <navigation />
    <div class="register-page">
      <div class="naslov">Registriraj se!</div>
      <form class="forma">
        <v-text-field
          v-model="username"
          label="Username"
          required
        ></v-text-field>
        <v-text-field v-model="password" label="Sifra" required></v-text-field>

        <v-btn class="mr-4" @click="register()">
          submit
        </v-btn>
      </form>
    </div>
  </div>
</template>

<script>
import AuthenticationService from "../services/AuthenticationService";
import Navigation from "./Navigation.vue";
export default {
  data() {
    Navigation;
    return {
      username: "",
      password: "",
    };
  },
  methods: {
    async register() {
      const response = await AuthenticationService.register({
        username: this.username,
        password: this.password,
      });
      this.$store.dispatch("setToken", response.data.token);
      this.$store.dispatch("setUser", response.data.user);
      this.$router.push({ name: "Home" });
    },
  },
};
</script>

<style scoped>
.main-container {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.register-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  margin-top: 10vh;
}

.forma {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.naslov {
  font-size: 26px;
}
</style>
