import Vue from "vue";
import Router from "vue-router";
import Home from "@/components/Home";
import Register from "@/components/Register";
import Dodajvijest from "@/components/Dodajvijest";
import aboutUs from "@/components/aboutUs";
import Login from "@/components/Login";
Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "Home",
      component: Home,
    },
    {
      path: "/register",
      name: "Register",
      component: Register,
    },
    {
      path: "/objavivijest",
      name: "Dodajvijest",
      component: Dodajvijest,
    },
    {
      path: "/aboutus",
      name: "aboutUs",
      component: aboutUs,
    },
    {
      path: "/login",
      name: "Login",
      component: Login,
    },
  ],
});
