<template>
  <div class="container">
    <input type="text" placeholder="Naslov" v-model="naslov" />
    <input type="text" placeholder="Podnaslov" v-model="podnaslov" />
    <input type="text" placeholder="Sadrzaj" v-model="sadrzaj" />
    <router-link to="/">
      <input type="submit" @click="posaljiZahtjev()" />
    </router-link>
  </div>
</template>

<script>
import Objavivijest from "../services/Objavivijest";
export default {
  data() {
    return {
      naslov: null,
      podnaslov: null,
      sadrzaj: null,
    };
  },
  methods: {
    async posaljiZahtjev() {
      await Objavivijest.objaviVijest({
        naslov: this.naslov,
        podnaslov: this.podnaslov,
        sadrzaj: this.sadrzaj,
      });
    },
  },
};
</script>

<style scoped></style>
